// pages / dashboard / property / manage

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class PropertyManage extends React.Component {
    componentDidMount() {
        $('.manage-facility-table').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]
        });

        $(".select-state").select2({
            placeholder: "Select State",
            allowClear: true,
            width: '100%'
        });
    }

    addNewProperty() {
        $('#modalNewProperty').modal('show');
    }

    editProperty() {
        $('#modalEditProperty').modal('show');
    }

    deleteConfirmProperty() {
        $('#modalDeleteProperty').modal('show');
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                    {/* Start of Helmet */}
                    <Helmet title="Property - manage" meta={[
                        {
                            name: 'description',
                            content: 'Property - manage'
                        }, {
                            name: 'keywords',
                            content: 'Property'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-8">
                            <h2>Manage Property</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Manage Property</strong>
                                </li>
                            </ol>
                        </div>
                    </div>
                    {/* End of Heading */}



                    {/* Start of Modal */}
                    <div className="modal inmodal" id="modalNewProperty" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated fadeIn">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-building-o modal-icon"></i>
                                    <h4 className="modal-title">Edit Property</h4>
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldName">Name *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Property Name" id="fieldName" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldAddress">Address *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Address" id="fieldAddress" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldZipcode">Zipcode *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Zipcode" id="fieldZipcode" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="fieldState" className="col-lg-2 control-label">State *</label>
                                            <div className="col-lg-10">
                                                <select className="select-state form-control" id="fieldState" defaultValue="" required >
                                                    <option></option>
                                                    <option value="Wp Kuala Lumpur">Wp Kuala Lumpur</option>
                                                    <option value="Wp Putrajaya">Wp Putrajaya</option>
                                                    <option value="Wp Labuan">Wp Labuan</option>
                                                    <option value="Johor">Johor</option>
                                                    <option value="Kedah">Kedah</option>
                                                    <option value="Kelantan">Kelantan</option>
                                                    <option value="Melaka">Melaka</option>
                                                    <option value="Negeri Sembilan">Negeri Sembilan</option>
                                                    <option value="Pahang">Pahang</option>
                                                    <option value="Penang">Penang</option>
                                                    <option value="Perak">Perak</option>
                                                    <option value="Perlis">Perlis</option>
                                                    <option value="Sabah">Sabah</option>
                                                    <option value="Sarawak">Sarawak</option>
                                                    <option value="Selangor">Selangor</option>
                                                    <option value="Terengganu">Terengganu</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldCity">City *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="City" id="fieldCity" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary">Save Changes</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal inmodal" id="modalEditProperty" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated fadeIn">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-building-o modal-icon"></i>
                                    <h4 className="modal-title">Edit Property</h4>
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldName">Name *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Property Name" id="fieldName" className="form-control" defaultValue="Agile Mont Kiara" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldAddress">Address *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Address" id="fieldAddress" className="form-control" defaultValue="Jalan Duta Kiara, Taman Sri Hartamas" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldZipcode">Zipcode *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Zipcode" id="fieldZipcode" className="form-control" defaultValue="50480" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="fieldState" className="col-lg-2 control-label">State *</label>
                                            <div className="col-lg-10">
                                                <select className="select-state form-control" id="fieldState" defaultValue="Wp Kuala Lumpur" required >
                                                    <option></option>
                                                    <option value="Wp Kuala Lumpur">Wp Kuala Lumpur</option>
                                                    <option value="Wp Putrajaya">Wp Putrajaya</option>
                                                    <option value="Wp Labuan">Wp Labuan</option>
                                                    <option value="Johor">Johor</option>
                                                    <option value="Kedah">Kedah</option>
                                                    <option value="Kelantan">Kelantan</option>
                                                    <option value="Melaka">Melaka</option>
                                                    <option value="Negeri Sembilan">Negeri Sembilan</option>
                                                    <option value="Pahang">Pahang</option>
                                                    <option value="Penang">Penang</option>
                                                    <option value="Perak">Perak</option>
                                                    <option value="Perlis">Perlis</option>
                                                    <option value="Sabah">Sabah</option>
                                                    <option value="Sarawak">Sarawak</option>
                                                    <option value="Selangor">Selangor</option>
                                                    <option value="Terengganu">Terengganu</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldCity">City *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="City" id="fieldCity" className="form-control" defaultValue="Kuala Lumpur" required />
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary">Save Changes</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal inmodal" id="modalDeleteProperty" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated bounce">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-exclamation-triangle modal-icon"></i>
                                    <h4 className="modal-title">Are you sure?</h4>
                                    This action is irreversible!
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-danger">Proceed</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Modal */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInDown">
                        <div className="row">
                            <div className="col-lg-8 col-hcenter">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Property Table</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                    <i className="fa fa-times"></i>
                                                </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <button type="button" className="btn btn-w-m btn-primary pull-right" onClick={this.addNewProperty.bind(this)}>
                                            Add New Property
                                        </button>
                                        <div className="clearfix space-15"></div>
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover manage-facility-table">
                                                <thead>
                                                    <tr>
                                                        <th>Property Name</th>
                                                        <th>Address</th>
                                                        <th>Zipcode</th>
                                                        <th>City</th>
                                                        <th>Edit Property</th>
                                                        <th>Delete Property</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Banyan Tree
                                                        </td>
                                                        <td>
                                                            KLCC - Jalan Conlay, Klcc Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            55100
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editProperty.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmProperty.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Agile Mont Kiara
                                                        </td>
                                                        <td>
                                                            Jalan Duta Kiara, Taman Sri Hartamas
                                                        </td>
                                                        <td>
                                                            50480
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editProperty.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmProperty.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            casaman
                                                        </td>
                                                        <td>
                                                            Desa ParkCity, casaman
                                                        </td>
                                                        <td>
                                                            55100
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editProperty.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmProperty.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            The Breezeway
                                                        </td>
                                                        <td>
                                                            Jalan Residen 2, Desa ParkCity
                                                        </td>
                                                        <td>
                                                            52200
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editProperty.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmProperty.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            M Vertica
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur, Cheras
                                                        </td>
                                                        <td>
                                                            55100
                                                        </td>
                                                        <td>
                                                            Kuala Lumpur
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editProperty.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmProperty.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(PropertyManage);



